import { Image, ScrollView, StyleSheet, Text, TouchableOpacity, View } from 'react-native'
import React from 'react'

const Nursery = ({navigation}) => {
  return (
   <ScrollView>
      {/* Study Material navigation */}
      <View style={styles.StudyMaterialContainer}>
            {/* Nursary Content */}
            <TouchableOpacity onPress={() => navigation.navigate("A to Z")}>
                <View style={styles.Word_Library_Container}>
                    <Image style={{ height: "100%", width: "100%", borderRadius: 10,resizeMode: 'contain', }} source={{ uri: "https://charmingprogrammer.github.io/HCC/HCC_A_To_Z_Pic/20220526_230747_0000.png" }} />
                </View>
            </TouchableOpacity>
            </View>
   </ScrollView>
  )
}

export default Nursery

const styles = StyleSheet.create({
  StudyMaterialContainer: {
    width:'100%',
    flexDirection: "row",
    justifyContent: "space-between",
    alignItems: "center",
    flexWrap: "wrap",
},
Word_Library_Container: {
    height: 150,
    width: 200,
    transform: [{ scale: 0.9}],
    borderRadius: 10,
    borderColor: '#38b6ff',
},
})