import {
  View,
  Text,
  StyleSheet,
  Image,
  ScrollView,
  Button,
  TouchableOpacity,
  FlatList,
  RefreshControl,
  ActivityIndicator
} from "react-native";
import React, { useEffect, useState } from "react";
import { createNativeStackNavigator } from "@react-navigation/native-stack";
import call from "react-native-phone-call";
import {
  AdMobBanner,
  AdMobInterstitial,
  PublisherBanner,
  AdMobRewarded,
  setTestDeviceIDAsync,
} from "expo-ads-admob";

// for controlling the pull to refresh functionllity
const wait = (timeout) => {
  return new Promise((resolve) => setTimeout(resolve, timeout));
};

const Stack = createNativeStackNavigator();
const HomeScreen = ({ navigation }) => {
  const [inputValue, setInputValue] = useState("8207529736");

  const triggerCall = () => {
    // Check for perfect 10 digit length
    if (inputValue.length != 10) {
      alert("Please insert correct contact number");
      return;
    }

    const args = {
      number: inputValue,
      prompt: true,
    };
    // Make a call
    call(args).catch(console.error);
  };

  const [refreshing, setRefreshing] = React.useState(false);

  const onRefresh = React.useCallback(() => {
    setRefreshing(true);
    wait(2000).then(() => setRefreshing(false));
  }, []);

  const [myUserData, SetMyUserData] = useState();
  const [isLoaded, setisLoaded] = useState(true);
  const getUserData = async () => {
    try {
      const response = await fetch(
        "https://charmingprogrammer.github.io/HCC/StudentsData.json"
      );
      const data = await response.json();
      SetMyUserData(data);
      setisLoaded(false);
    } catch (error) {
      console.log(error);
    }
  };

  useEffect(() => {
    getUserData();
  }, []);

  return (
    <ScrollView
      refreshControl={
        <RefreshControl refreshing={refreshing} onRefresh={onRefresh} />
      }
    >
      <View style={style.container}>
        {/* poster */}
        <View style={style.imageContainer}>
          <Image
            source={{
              uri: "https://charmingprogrammer.github.io/HCC/20220525_113746_0000.png",
            }}
            style={style.image}
          />
        </View>
        {/* slug */}
        <View style={style.slugContainer}>
          <Text style={style.slug}>Welcome To HCC</Text>
          <Text style={style.slug2}>Katra's smartest tuition classes.</Text>

          <TouchableOpacity onPress={triggerCall}>
            <Text
              style={{
                marginTop: 20,
                backgroundColor: "#38b6ff",
                color: "white",
                borderRadius: 5,
                padding: 10,
              }}
            >
              Admission Now
            </Text>
          </TouchableOpacity>
        </View>
        <AdMobBanner
        bannerSize="fullBanner"
        adUnitID="ca-app-pub-4365651039511906/7584231926" // Test ID, Replace with your-admob-unit-id
        servePersonalizedAds // true or false
      />

        {isLoaded ? (
         <ActivityIndicator size="large" color="#38b6ff" style={{marginTop:10,}}/>
        ) : (
          <View style={style.studentDataContainer}>
            <FlatList
              data={myUserData}
              renderItem={({ item }) => (
                <View style={style.studentData}>
                  {/* user image */}
                  <Image
                    source={{ uri: item.Pic }}
                    style={{ height: 150, width: 150, borderRadius: 100,    borderWidth : 2,
                        borderColor: '#38b6ff', }}
                  />
                  {/* user name */}
                  <Text
                    style={{
                      fontSize: 25,
                      fontWeight: "bold",
                      color: "#38b6ff",
                    }}
                  >
                    {item.Name}
                  </Text>
                  <Text style={{ fontSize: 20 }}>
                    Joining Date : {item.Joining}
                  </Text>
                  <Text style={{ fontSize: 20 }}>
                    Section : {item.section} | Status : {item.Status}
                  </Text>
                </View>
              )}
            />
          </View>
        )}
        
      </View>
      
    </ScrollView>
  );
};

export default HomeScreen;

const style = StyleSheet.create({
  container: {
    // flex:1,
    // backgroundColor:'#fff',
    // alignItems:'flex-start',
    // justifyContent:'flex-start'
  },
  imageContainer: {
    width: "100%",
    height: 300,
    resizeMode: "contain",
    backgroundColor: "#fff",
  },
  image: {
    width: "100%",
    height: "100%",
  },
  slugContainer: {
    width: "100%",
    height: 200,
    backgroundColor: "#fff",
    borderRadius: 10,
    padding: 10,
    alignItems: "center",
    justifyContent: "center",
  },
  slug: {
    fontSize: 25,
    fontWeight: "bold",
    color: "#38b6ff",
    textAlign: "center",
  },
  slug2: {
    fontSize: 20,
  },
  studentDataContainer: {
    width: "100%",
    height: "100%",
  },
  studentData: {
    width: "100%",
    height: 500,
    backgroundColor: "white",
    transform: [{ scale: 0.95 }],
    borderRadius: 10,
    flex: 1,
    justifyContent: "center",
    alignItems: "center",
    borderWidth : 1,
    borderColor: '#38b6ff',
  },
});
