import {
  StyleSheet,
  Text,
  View,
  Image,
  ScrollView,
  FlatList,
  TouchableOpacity,
  ActivityIndicator,
} from "react-native";
import React, { useEffect, useState } from "react";
import {
  AdMobBanner,
  AdMobInterstitial,
  PublisherBanner,
  AdMobRewarded,
  setTestDeviceIDAsync,
} from "expo-ads-admob";

const WordMeaning = () => {
  // fetching  the data from the json file
  const [myUserData, SetMyUserData] = useState();
  const [isLoaded, setisLoaded] = useState(true);
  const getUserData = async () => {
    try {
      const response = await fetch(
        "https://charmingprogrammer.github.io/HCC/WordMeaning.json"
      );
      const data = await response.json();
      SetMyUserData(data);
      setisLoaded(false);
    } catch (error) {
      console.log(error);
    }
  };

  useEffect(() => {
    getUserData();
  }, []);

  return (
    <View>
           <AdMobBanner
        bannerSize="fullBanner"
        adUnitID="ca-app-pub-4365651039511906/7584231926" // Test ID, Replace with your-admob-unit-id
        servePersonalizedAds // true or false
      />


      {isLoaded ? (
        <ActivityIndicator
          style={{ marginTop: 30 }}
          size="large"
          color="#38b6ff"
        />
      ) : (
        <FlatList
          data={myUserData}
          snapToAlignment="center"
          decelerationRate={"fast"}
          snapToInterval={750}
          renderItem={({ item }) => (
            <View style={styles.MeaningContainer}>
              <View
                style={{
                  backgroundColor: "white",
                  padding: 10,
                  color: "white",
                  borderRadius: 100,
                  fontWeight: "900",
                  justifyContent: "center",
                  alignItems: "center",
                }}
              >
                <Text
                  style={{
                    backgroundColor: "#38b6ff",
                    fontSize: 15,
                    paddingHorizontal: 25,
                    paddingVertical: 10,
                    color: "white",
                    borderRadius: 100,
                    fontWeight: "900",
                  }}
                >
                  Date : {item.Date}
                </Text>
              </View>
              <Text style={styles.Meaning}>1. {item.first}</Text>
              <Text style={styles.Meaning}>2. {item.second}</Text>
              <Text style={styles.Meaning}>3. {item.third}</Text>
              <Text style={styles.Meaning}>4. {item.forth}</Text>
              <Text style={styles.Meaning}>5. {item.fifth}</Text>
              <Text style={styles.Meaning}>6. {item.sixth}</Text>
              <Text style={styles.Meaning}>7. {item.seventh}</Text>
              <Text style={styles.Meaning}>8. {item.eighth}</Text>
              <Text style={styles.Meaning}>9. {item.nineth}</Text>
              <Text style={styles.Meaning}>10. {item.tenth}</Text>
            </View>
            
          )}
        />
      )}
    </View>
  );
};

export default WordMeaning;

const styles = StyleSheet.create({
  MeaningContainer: {
    backgroundColor: "white",
    padding: 10,
    transform: [{ scale: 0.95 }],
    borderRadius: 10,
    borderColor: "#38b6ff",
    borderWidth:1,
    height: 750,
  },
  Meaning: {
    fontSize: 20,
    padding: 10,
    backgroundColor: "white",
    borderLeftWidth:5,
    borderLeftColor:"#38b6ff",
    borderRightWidth:5,
    borderRightColor:"#38b6ff",
    borderBottomWidth:1,
    borderBottomColor:"#38b6ff",
    borderTopWidth:1,
    borderTopColor:"#38b6ff",
    borderRadius:10,
    marginTop:10,
  },
});
