import { StyleSheet, Text, View, TouchableOpacity } from 'react-native';
import { NavigationContainer } from '@react-navigation/native';
import { createNativeStackNavigator } from '@react-navigation/native-stack';
import { createBottomTabNavigator } from '@react-navigation/bottom-tabs';
import Ionicons from 'react-native-vector-icons/Ionicons';
import HomeScreen from './components/HomeScreen';
import StudyMaterial from './components/StudyMaterial';
import Timetable from './components/Timetable';
import Nursery from './components/Nursery';
import UKG from './components/UKG';
import Class1 from './components/Class1';
import Mathematics1 from './components/Class 1/Mathematics1';
import Science1 from './components/Class 1/Science1';
import Computer1 from './components/Class 1/Computer1';
import Result from './components/Result';
import About from './components/About';
import WordMeaning from './components/WordMeaning';
import Spoken from './components/Spoken';
import GK from './components/GK';
import AToZ from './components/Nursery/AToZ';





// Navigation bar function for tab and stack navigation.
const Stack = createNativeStackNavigator();
const Tab = createBottomTabNavigator();

// main home that helps us to naviate stack and tab naviagtion.
function Home() {
  return (
    <Tab.Navigator initialRouteName={HomeScreen} screenOptions={({ route }) => ({
      tabBarIcon: ({ focused, color, size }) => {
        let iconName;

        if (route.name === 'Home') {
          iconName = focused
            ? 'home-sharp'
            : 'home-outline';
        }
        else if (route.name === 'Study Material') {
          iconName = focused ? 'ios-book' : 'ios-book-outline';
        }
        else if (route.name === 'Timetable') {
          iconName = focused ? 'ios-alarm' : 'ios-alarm-outline';
        }
        else if (route.name === 'Result') {
          iconName = focused ? 'ios-ribbon' : 'ios-ribbon-outline';
        }
        else if (route.name === 'About') {
          iconName = focused ? 'ios-person' : 'ios-person-outline';
        }

        // You can return any component that you like here!
        return <Ionicons name={iconName} size={size} color={color} />;
      },
      tabBarActiveTintColor: '#38b6ff',
      tabBarInactiveTintColor: 'gray',
    })}>
      <Tab.Screen name="Home" component={HomeScreen} options={{
        title: `HCC Katra`,
        headerStyle: {
          backgroundColor: '#38b6ff',
        },
        headerTintColor: 'white',
        headerTitleStyle: {
          fontWeight: 'bold',
          fontSize: 20,
          // fontFamily: 'sans-serif-condensed',
        },
      }} />
      <Tab.Screen name="Study Material" component={StudyMaterial} options={{ headerStyle:{ backgroundColor: '#38b6ff'},headerTintColor: 'white',}}/>
      <Tab.Screen name="Timetable" component={Timetable} />
      <Tab.Screen name="Result" component={Result} />
      <Tab.Screen name="About" component={About} />
    </Tab.Navigator>
  )
}

export default function App() {
  return (
    <NavigationContainer>
        <Stack.Navigator>
          <Stack.Screen name="H" component={Home} options={{ headerShown: false }} />
          <Stack.Screen name="HCC Words Meanings" component={WordMeaning} />
          <Stack.Screen name="HCC Spoken" component={Spoken} />
          <Stack.Screen name='HCC G.K' component={GK} />
          <Stack.Screen name="Nursery" component={Nursery} />
          <Stack.Screen name="UKG" component={UKG} />
          <Stack.Screen name="Class 1" component={Class1} />
          <Stack.Screen name="Mathematics 1" component={Mathematics1} />
          <Stack.Screen name="Science 1" component={Science1} />
          <Stack.Screen name="Computer 1" component={Computer1} />
          <Stack.Screen name="A to Z" component={AToZ}/>
        </Stack.Navigator>
    </NavigationContainer>
  );
}

