import {
  StyleSheet,
  Text,
  View,
  FlatList,
  ScrollView,
  ActivityIndicator,
  RefreshControl,
} from "react-native";
import React, { useEffect, useState } from "react";
import {
  AdMobBanner,
  AdMobInterstitial,
  PublisherBanner,
  AdMobRewarded,
  setTestDeviceIDAsync,
} from "expo-ads-admob";

const Result = () => {
  // for controlling the pull to refresh functionllity
  const wait = (timeout) => {
    return new Promise((resolve) => setTimeout(resolve, timeout));
  };
  const [refreshing, setRefreshing] = React.useState(false);
  const onRefresh = React.useCallback(() => {
    setRefreshing(true);
    getUserData();
    setRefreshing(false);
  }, []);

  const [myUserData, SetMyUserData] = useState();
  const [isLoaded, setisLoaded] = useState(true);
  const getUserData = async () => {
    try {
      const response = await fetch(
        "https://charmingprogrammer.github.io/HCC/Result.json"
      );
      const data = await response.json();
      SetMyUserData(data);
      setisLoaded(false);
    } catch (error) {
      console.log(error);
    }
  };
  

  useEffect(() => {
    getUserData();
  }, []);
  return (
    <View
      refreshControl={
        <RefreshControl refreshing={refreshing} onRefresh={onRefresh} />
      }
    >
           <AdMobBanner
        bannerSize="fullBanner"
        adUnitID="ca-app-pub-4365651039511906/7584231926" // Test ID, Replace with your-admob-unit-id
        servePersonalizedAds // true or false
      />

      <View style={styles.container}>
        {/* Rank  */}
        <View
          style={{
            padding: 10,
            marginTop: 10,
            backgroundColor: "#38b6ff",
            width: "15%",
            transform: [{ scale: 0.98 }],
            borderRadius:10,
          }}
        >
          <Text style={{ color: "white" }}>Rank</Text>
        </View>
        {/* Student Name */}
        <View
          style={{
            padding: 10,
            marginTop: 10,
            backgroundColor: "#38b6ff",
            width: "35%",
            transform: [{ scale: 0.98 }],
            borderRadius:10,
          }}
        >
          <Text style={{ color: "white" }}>Student Name</Text>
        </View>
        {/* Percentage */}
        <View
          style={{
            padding: 10,
            marginTop: 10,
            backgroundColor: "#38b6ff",
            width: "25%",
            transform: [{ scale: 0.98 }],
            borderRadius:10,
          }}
        >
          <Text style={{ color: "white" }}>Percentage</Text>
        </View>
        {/* Division */}
        <View
          style={{
            padding: 10,
            marginTop: 10,
            backgroundColor: "#38b6ff",
            width: "25%",
            transform: [{ scale: 0.98 }],
            borderRadius:10,
          }}
        >
          <Text style={{ color: "white" }}>Division</Text>
        </View>
      </View>
      {isLoaded ? (
        <ActivityIndicator
          style={{ marginTop: 30 }}
          size="large"
          color="dodgerblue"
        />
      ) : (
        <FlatList
        style={{marginBottom:15}}
          data={myUserData}
          renderItem={({ item }) => (
            <View style={styles.container}>
              {/* Rank  */}
              <View
                style={{
                  padding: 10,
                  marginTop: 10,
                  backgroundColor: "#cbe2f9",
                  width: "15%",
                  transform: [{ scale: 0.98 }],
                  borderRadius:10,
                }}
              >
                <Text style={styles.text}>{item.Rank}</Text>
              </View>
              {/* Student Name */}
              <View
                style={{
                  padding: 10,
                  marginTop: 10,
                  backgroundColor: "#cbe2f9",
                  width: "35%",
                  transform: [{ scale: 0.98 }],
                  borderRadius:10,
                }}
              >
                <Text style={styles.text}>{item.Name}</Text>
              </View>
              {/* Percentage */}
              <View style={styles.box}>
                <Text style={styles.text}>{item.Percentage}</Text>
              </View>
              {/* Division */}
              <View style={styles.box}>
                <Text style={styles.text}>{item.Division}</Text>
              </View>
            </View>
          )}
        />
      )}
    </View>
  );
};

export default Result;

const styles = StyleSheet.create({
  container: {
    flexDirection: "row",
    justifyContent: "flex-start",
    alignItems: "center",
  },
  box: {
    padding: 10,
    marginTop: 10,
    backgroundColor: "#cbe2f9",
    width: "25%",
    transform: [{ scale: 0.98 }],
    borderRadius:10,
  },
  text: {
    color: "black",
  },
});
