import {
  StyleSheet,
  Text,
  View,
  Image,
  ScrollView,
  FlatList,
  TouchableOpacity,
  ActivityIndicator,
} from "react-native";
import React, { useEffect, useState } from "react";

import {
  AdMobBanner,
  AdMobInterstitial,
  PublisherBanner,
  AdMobRewarded,
  setTestDeviceIDAsync,
} from "expo-ads-admob";

const AToZ = () => {
  // fetching  the data from the json file
  const [myUserData, SetMyUserData] = useState();
  const [isLoaded, setisLoaded] = useState(true);
  const getUserData = async () => {
    try {
      const response = await fetch(
        "https://charmingprogrammer.github.io/HCC/AtoZImg.json"
      );
      const data = await response.json();
      SetMyUserData(data);
      setisLoaded(false);
    } catch (error) {
      console.log(error);
    }
  };

  useEffect(() => {
    getUserData();
  }, []);

  return (
    <View>
           <AdMobBanner
        bannerSize="fullBanner"
        adUnitID="ca-app-pub-4365651039511906/7584231926" // Test ID, Replace with your-admob-unit-id
        servePersonalizedAds // true or false
      />
      {isLoaded ? (
        <ActivityIndicator
          style={{ marginTop: 30 }}
          size="large"
          color="#38b6ff"
        />
      ) : (
        <FlatList
          data={myUserData}
          snapToAlignment="center"
          decelerationRate={"fast"}
          snapToInterval={750}
          renderItem={({ item }) => (
            <View style={styles.Img_container}>
              <Image source={{ uri: item.Image }} style={styles.Img} />
            </View>
          )}
        />
      )}

    </View>
  );
};

export default AToZ;

const styles = StyleSheet.create({
  Img_container: {
    backgroundColor: "white",
    height: 750,
    borderBottomWidth: 5,
    borderBottomColor: "black",
  },
  Img: {
    height: "100%",
    width: "100%",
  },
});
