import { StyleSheet, Text, View, Image, ScrollView, FlatList, TouchableOpacity,ActivityIndicator } from 'react-native'
import React, { useEffect, useState } from 'react'
import {
    AdMobBanner,
    AdMobInterstitial,
    PublisherBanner,
    AdMobRewarded,
    setTestDeviceIDAsync,
  } from "expo-ads-admob";


export default function StudyMaterial({ navigation }) {
    return (
        <ScrollView>
            {/* for regular content  */}
            <View style={{height:70, width:'100%', backgroundColor:'#38b6ff', flexDirection:'row',justifyContent:'center', alignItems:'center'}}>
                {/* Word meanings  */}
                <TouchableOpacity onPress={()=> navigation.navigate("HCC Words Meanings")}>
                    <Text style={{backgroundColor: "white", padding:10, fontWeight:'900', fontSize:18, transform:[{scale:0.9}],borderRadius:100,}}>Word Meaning</Text>
                </TouchableOpacity>
                <TouchableOpacity onPress={() => navigation.navigate("HCC Spoken")}>
                    <Text style={{backgroundColor: "white", padding:10, fontWeight:'900', fontSize:18, transform:[{scale:0.9}],borderRadius:100,}}>Spoken</Text>
                </TouchableOpacity>
                <TouchableOpacity onPress={() => navigation.navigate("HCC G.K")}>
                    <Text style={{backgroundColor: "white", padding:10, fontWeight:'900', fontSize:18, transform:[{scale:0.9}],borderRadius:100,}}>G.K</Text>
                </TouchableOpacity>
                {/* Spoken  */}
                {/* G.k */}

            </View>
            <AdMobBanner
        bannerSize="fullBanner"
        adUnitID="ca-app-pub-4365651039511906/7584231926" // Test ID, Replace with your-admob-unit-id
        servePersonalizedAds // true or false
      />
            {/* Study Material navigation */}
            <View style={styles.StudyMaterialContainer}>
            {/* Nursary Content */}
            <TouchableOpacity onPress={() => navigation.navigate("Nursery")}>
                <View style={styles.Word_Library_Container}>
                    <Image style={{ height: "100%", width: "100%", borderRadius: 10,resizeMode: 'contain', }} source={{ uri: "https://charmingprogrammer.github.io/HCC/20220524_150945_0000.png" }} />
                </View>
            </TouchableOpacity>
            {/* UKG Content */}
            <TouchableOpacity onPress={() => navigation.navigate("UKG")}>
                <View style={styles.Word_Library_Container}>
                    <Image style={{ height: "100%", width: "100%", borderRadius: 10,resizeMode: 'contain', }} source={{ uri: "https://charmingprogrammer.github.io/HCC/20220524_151832_0000.png" }} />
                </View>
            </TouchableOpacity>
            {/* Class 01 Content */}
            <TouchableOpacity onPress={() => navigation.navigate("Class 1")}>
                <View style={styles.Word_Library_Container}>
                    <Image style={{ height: "100%", width: "100%", borderRadius: 10,resizeMode: 'contain', }} source={{ uri: "https://charmingprogrammer.github.io/HCC/20220524_152139_0000.png" }} />
                </View>
            </TouchableOpacity>
            </View>
        </ScrollView>
    )
}

const styles = StyleSheet.create({
    StudyMaterialContainer: {
        width:'100%',
        flexDirection: "row",
        justifyContent: "space-between",
        alignItems: "center",
        flexWrap: "wrap",
    },
    Word_Library_Container: {
        height: 150,
        width: 200,
        transform: [{ scale: 0.9}],
        borderRadius: 10,
        borderColor: '#38b6ff',
    },
})