import {
  StyleSheet,
  Text,
  View,
  Image,
  Linking,
  TouchableOpacity,
  ScrollView,
} from "react-native";
import React from "react";
import Ionicons from "react-native-vector-icons/Ionicons";

const About = () => {
  return (
    <ScrollView>
      {/* founder profile */}
      <View style={styles.founder_profile}>
        <Image
          style={styles.founder_image}
          source={{
            uri: "https://simgbb.com/avatar/M5zLLq7PnVGF.png",
          }}
        />
        <Text style={{ fontWeight: "bold", fontSize: 25, color: "white" }}>
          Manohar Anand
        </Text>
        <Text style={{ fontWeight: "bold", fontSize: 20, color: "white" }}>
          Founder
        </Text>
      </View>
      {/* Follow Me */}
      <Text
        style={{
          backgroundColor: "#38b6ff",
          fontSize: 25,
          padding: 10,
          marginTop: 10,
          marginBottom: 10,
          color: "white",
          transform: [{ scale: 0.95 }],
          borderRadius: 10,
          fontWeight: "bold",
          textAlign: "center",
        }}
      >
        Follow Me
      </Text>
      <View style={styles.follow_me_container}>
        <TouchableOpacity
          style={styles.follow_icon}
          onPress={() =>
            Linking.openURL("https://www.facebook.com/Manoarya.manohar/")
          }
        >
          <Ionicons
            name="logo-facebook"
            style={{
              fontSize: 30,
              color: "dodgerblue",
            }}
          />
          <Text>Facebook</Text>
        </TouchableOpacity>
        <TouchableOpacity
          style={styles.follow_icon}
          onPress={() =>
            Linking.openURL(
              "https://twitter.com/Manohar_Anand_?t=XENteac-yNJ1dmiR2PiKaw&s=09"
            )
          }
        >
          <Ionicons
            name="logo-twitter"
            style={{
              fontSize: 30,
              color: "dodgerblue",
            }}
          />
          <Text>Twitter</Text>
        </TouchableOpacity>
        <TouchableOpacity
          style={styles.follow_icon}
          onPress={() =>
            Linking.openURL(
              "https://www.instagram.com/manohar_anand_insta/?hl=en"
            )
          }
        >
          <Ionicons
            name="logo-instagram"
            style={{
              fontSize: 30,
              color: "pink",
            }}
          />
          <Text>Instagram</Text>
        </TouchableOpacity>
        <TouchableOpacity
          style={styles.follow_icon}
          onPress={() =>
            Linking.openURL(
              "https://youtube.com/channel/UC7MDn7ukjwk8ZScoBpkR-Dg"
            )
          }
        >
          <Ionicons
            name="logo-youtube"
            style={{
              fontSize: 30,
              color: "red",
            }}
          />
          <Text>YouTube</Text>
        </TouchableOpacity>
        <TouchableOpacity
          style={styles.follow_icon}
          onPress={() =>
            Linking.openURL(
              "https://www.linkedin.com/in/manohar-anand-4177701b6"
            )
          }
        >
          <Ionicons
            name="logo-linkedin"
            style={{
              fontSize: 30,
              color: "dodgerblue",
            }}
          />
          <Text>LinkedIn</Text>
        </TouchableOpacity>
      </View>
      {/* Important Pages */}
      <Text
        style={{
          backgroundColor: "#38b6ff",
          fontSize: 25,
          padding: 10,
          marginTop: 10,
          marginBottom: 10,
          color: "white",
          transform: [{ scale: 0.95 }],
          borderRadius: 10,
          fontWeight: "bold",
          textAlign: "center",
        }}
      >
        Important Pages
      </Text>
      <TouchableOpacity
        onPress={() =>
          Linking.openURL(
            "https://charmingprogrammer.github.io/HCC/AboutUS.html"
          )
        }
      >
        <Text style={styles.impo_pages}>About us</Text>
      </TouchableOpacity>
      <TouchableOpacity
        onPress={() =>
          Linking.openURL(
            "https://charmingprogrammer.github.io/HCC/ContactUs.html"
          )
        }
      >
        <Text style={styles.impo_pages}>Contact us</Text>
      </TouchableOpacity>
      <TouchableOpacity
        onPress={() =>
          Linking.openURL(
            "https://charmingprogrammer.github.io/HCC/Terms&Condition.html"
          )
        }
      >
        <Text style={styles.impo_pages}>Terms and Condition</Text>
      </TouchableOpacity>
      <TouchableOpacity
        onPress={() =>
          Linking.openURL(
            "https://charmingprogrammer.github.io/HCC/PrivacyPolicy.html"
          )
        }
      >
        <Text style={styles.impo_pages}>Privacy Policy</Text>
      </TouchableOpacity>
      <TouchableOpacity
        onPress={() =>
          Linking.openURL("https://charmingprogrammer.github.io/HCC/Disclamer")
        }
      >
        <Text style={styles.impo_pages}>Disclamer</Text>
      </TouchableOpacity>
    </ScrollView>
  );
};

export default About;

const styles = StyleSheet.create({
  founder_profile: {
    height: 300,
    width: "100%",
    justifyContent: "center",
    alignItems: "center",
    backgroundColor: "#38b6ff",
    transform: [{ scale: 0.95 }],
    borderRadius: 10,
  },
  founder_image: {
    height: 150,
    width: 150,
    borderRadius: 100,
  },
  follow_me_container: {
    height: 100,
    width: "100%",
    flexDirection: "row",
    justifyContent: "space-around",
    alignItems: "center",
    transform: [{ scale: 0.95 }],
  },
  follow_icon: {
    fontSize: 30,
    padding: 10,
    borderRadius: 10,
    justifyContent: "center",
    alignItems: "center",
  },
  impo_pages: {
    fontSize: 20,
    padding: 10,
    borderRadius: 10,
    justifyContent: "center",
    alignItems: "center",
    transform: [{ scale: 0.95 }],
    backgroundColor: "white",
  },
});
